'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Separator } from '@/components/ui/separator'
import { Switch } from '@/components/ui/switch'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { useRouter } from 'next/navigation'

export default function ProfilePage() {
  const [user, setUser] = useState({
    name: 'Demo User',
    email: 'demo@speedchallenge.com',
    joinDate: '2024-01-01',
    totalTrips: 42,
    totalDistance: 1250.5,
    totalTime: 3600, // in minutes
    bestSpeed: 85.2,
    averageSpeed: 45.8
  })

  const [formData, setFormData] = useState({
    name: user.name,
    email: user.email,
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  })

  const [settings, setSettings] = useState({
    notifications: true,
    locationSharing: false,
    dataCollection: true,
    darkMode: true
  })

  const [isEditing, setIsEditing] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error', text: string } | null>(null)
  const router = useRouter()

  useEffect(() => {
    // Load user data from localStorage or API
    const token = localStorage.getItem('speedchallenge_token')
    if (!token) {
      router.push('/auth/login')
    }
  }, [router])

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const handleSettingChange = (setting: string, value: boolean) => {
    setSettings(prev => ({
      ...prev,
      [setting]: value
    }))
  }

  const handleSaveProfile = async () => {
    setIsLoading(true)
    setMessage(null)

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      setUser(prev => ({
        ...prev,
        name: formData.name,
        email: formData.email
      }))

      setMessage({ type: 'success', text: 'Profile updated successfully!' })
      setIsEditing(false)
    } catch (error) {
      setMessage({ type: 'error', text: 'Failed to update profile. Please try again.' })
    } finally {
      setIsLoading(false)
    }
  }

  const handleChangePassword = async () => {
    if (formData.newPassword !== formData.confirmPassword) {
      setMessage({ type: 'error', text: 'New passwords do not match.' })
      return
    }

    if (formData.newPassword.length < 6) {
      setMessage({ type: 'error', text: 'Password must be at least 6 characters long.' })
      return
    }

    setIsLoading(true)
    setMessage(null)

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      setMessage({ type: 'success', text: 'Password changed successfully!' })
      setFormData(prev => ({
        ...prev,
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }))
    } catch (error) {
      setMessage({ type: 'error', text: 'Failed to change password. Please try again.' })
    } finally {
      setIsLoading(false)
    }
  }

  const handleLogout = () => {
    localStorage.removeItem('speedchallenge_token')
    localStorage.removeItem('speedchallenge_onboarding_complete')
    router.push('/auth/login')
  }

  const handleDeleteAccount = () => {
    if (confirm('Are you sure you want to delete your account? This action cannot be undone.')) {
      localStorage.clear()
      router.push('/auth/register')
    }
  }

  const formatTime = (minutes: number) => {
    const hours = Math.floor(minutes / 60)
    const mins = minutes % 60
    return `${hours}h ${mins}m`
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-950 via-gray-900 to-black">
      {/* Background Effects */}
      <div className="absolute inset-0 bg-[linear-gradient(rgba(0,255,255,0.03)_1px,transparent_1px),linear-gradient(90deg,rgba(0,255,255,0.03)_1px,transparent_1px)] bg-[size:50px_50px]" />
      
      <div className="relative z-10 container mx-auto px-4 py-8 max-w-4xl">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-cyan-400 to-blue-500 font-['Orbitron'] mb-2">
            Profile
          </h1>
          <p className="text-gray-400 font-['Rajdhani'] text-lg">
            Manage your account and view your statistics
          </p>
        </div>

        {message && (
          <Alert className={`mb-6 ${message.type === 'success' ? 'border-green-500/50 bg-green-500/10' : 'border-red-500/50 bg-red-500/10'}`}>
            <AlertDescription className={`${message.type === 'success' ? 'text-green-400' : 'text-red-400'} font-['Rajdhani']`}>
              {message.text}
            </AlertDescription>
          </Alert>
        )}

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* User Stats */}
          <Card className="bg-gray-900/50 border-gray-700/50 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-xl text-gray-200 font-['Orbitron'] flex items-center justify-between">
                Your Statistics
                <Badge className="bg-gradient-to-r from-cyan-500 to-blue-600 text-white font-['Rajdhani']">
                  Level 5
                </Badge>
              </CardTitle>
              <CardDescription className="text-gray-400 font-['Rajdhani']">
                Your driving performance overview
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="grid grid-cols-2 gap-4">
                <div className="text-center p-4 bg-gray-800/30 rounded-lg">
                  <div className="text-2xl font-bold text-cyan-400 font-['Orbitron']">
                    {user.totalTrips}
                  </div>
                  <div className="text-sm text-gray-400 font-['Rajdhani']">Total Trips</div>
                </div>
                <div className="text-center p-4 bg-gray-800/30 rounded-lg">
                  <div className="text-2xl font-bold text-blue-400 font-['Orbitron']">
                    {user.totalDistance.toFixed(1)}km
                  </div>
                  <div className="text-sm text-gray-400 font-['Rajdhani']">Distance</div>
                </div>
                <div className="text-center p-4 bg-gray-800/30 rounded-lg">
                  <div className="text-2xl font-bold text-purple-400 font-['Orbitron']">
                    {formatTime(user.totalTime)}
                  </div>
                  <div className="text-sm text-gray-400 font-['Rajdhani']">Total Time</div>
                </div>
                <div className="text-center p-4 bg-gray-800/30 rounded-lg">
                  <div className="text-2xl font-bold text-pink-400 font-['Orbitron']">
                    {user.bestSpeed}km/h
                  </div>
                  <div className="text-sm text-gray-400 font-['Rajdhani']">Best Speed</div>
                </div>
              </div>

              <div>
                <div className="flex justify-between items-center mb-2">
                  <span className="text-sm text-gray-400 font-['Rajdhani']">Progress to Level 6</span>
                  <span className="text-sm text-cyan-400 font-['Rajdhani']">75%</span>
                </div>
                <Progress value={75} className="h-2 bg-gray-800" />
              </div>

              <div className="text-center">
                <p className="text-gray-400 font-['Rajdhani']">
                  Member since {new Date(user.joinDate).toLocaleDateString()}
                </p>
              </div>
            </CardContent>
          </Card>

          {/* Profile Settings */}
          <Card className="bg-gray-900/50 border-gray-700/50 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-xl text-gray-200 font-['Orbitron']">
                Profile Information
              </CardTitle>
              <CardDescription className="text-gray-400 font-['Rajdhani']">
                Update your personal information
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="name" className="text-gray-300 font-['Rajdhani'] font-medium">
                  Full Name
                </Label>
                <Input
                  id="name"
                  name="name"
                  value={formData.name}
                  onChange={handleInputChange}
                  disabled={!isEditing}
                  className="bg-gray-800/50 border-gray-600/50 text-gray-200 placeholder-gray-500 focus:border-cyan-500/50 focus:ring-cyan-500/20 font-['Rajdhani'] disabled:opacity-60"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="email" className="text-gray-300 font-['Rajdhani'] font-medium">
                  Email
                </Label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  disabled={!isEditing}
                  className="bg-gray-800/50 border-gray-600/50 text-gray-200 placeholder-gray-500 focus:border-cyan-500/50 focus:ring-cyan-500/20 font-['Rajdhani'] disabled:opacity-60"
                />
              </div>

              <div className="flex space-x-2">
                {!isEditing ? (
                  <Button
                    onClick={() => setIsEditing(true)}
                    className="bg-gradient-to-r from-cyan-500 to-blue-600 hover:from-cyan-600 hover:to-blue-700 text-white font-['Rajdhani']"
                  >
                    Edit Profile
                  </Button>
                ) : (
                  <>
                    <Button
                      onClick={handleSaveProfile}
                      disabled={isLoading}
                      className="bg-gradient-to-r from-green-500 to-green-600 hover:from-green-600 hover:to-green-700 text-white font-['Rajdhani']"
                    >
                      {isLoading ? 'Saving...' : 'Save Changes'}
                    </Button>
                    <Button
                      onClick={() => {
                        setIsEditing(false)
                        setFormData({
                          ...formData,
                          name: user.name,
                          email: user.email
                        })
                      }}
                      variant="outline"
                      className="border-gray-600/50 text-gray-300 hover:bg-gray-800/50 font-['Rajdhani']"
                    >
                      Cancel
                    </Button>
                  </>
                )}
              </div>
            </CardContent>
          </Card>

          {/* Privacy Settings */}
          <Card className="bg-gray-900/50 border-gray-700/50 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-xl text-gray-200 font-['Orbitron']">
                Privacy Settings
              </CardTitle>
              <CardDescription className="text-gray-400 font-['Rajdhani']">
                Control your data and privacy preferences
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="flex items-center justify-between">
                <div>
                  <Label className="text-gray-300 font-['Rajdhani'] font-medium">
                    Push Notifications
                  </Label>
                  <p className="text-sm text-gray-400 font-['Rajdhani']">
                    Receive challenge updates and achievements
                  </p>
                </div>
                <Switch
                  checked={settings.notifications}
                  onCheckedChange={(checked) => handleSettingChange('notifications', checked)}
                />
              </div>

              <Separator className="bg-gray-700/50" />

              <div className="flex items-center justify-between">
                <div>
                  <Label className="text-gray-300 font-['Rajdhani'] font-medium">
                    Location Sharing
                  </Label>
                  <p className="text-sm text-gray-400 font-['Rajdhani']">
                    Share your location for social features
                  </p>
                </div>
                <Switch
                  checked={settings.locationSharing}
                  onCheckedChange={(checked) => handleSettingChange('locationSharing', checked)}
                />
              </div>

              <Separator className="bg-gray-700/50" />

              <div className="flex items-center justify-between">
                <div>
                  <Label className="text-gray-300 font-['Rajdhani'] font-medium">
                    Data Collection
                  </Label>
                  <p className="text-sm text-gray-400 font-['Rajdhani']">
                    Help improve the app with usage analytics
                  </p>
                </div>
                <Switch
                  checked={settings.dataCollection}
                  onCheckedChange={(checked) => handleSettingChange('dataCollection', checked)}
                />
              </div>
            </CardContent>
          </Card>

          {/* Security Settings */}
          <Card className="bg-gray-900/50 border-gray-700/50 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-xl text-gray-200 font-['Orbitron']">
                Security
              </CardTitle>
              <CardDescription className="text-gray-400 font-['Rajdhani']">
                Manage your account security
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="currentPassword" className="text-gray-300 font-['Rajdhani'] font-medium">
                  Current Password
                </Label>
                <Input
                  id="currentPassword"
                  name="currentPassword"
                  type="password"
                  value={formData.currentPassword}
                  onChange={handleInputChange}
                  placeholder="Enter current password"
                  className="bg-gray-800/50 border-gray-600/50 text-gray-200 placeholder-gray-500 focus:border-cyan-500/50 focus:ring-cyan-500/20 font-['Rajdhani']"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="newPassword" className="text-gray-300 font-['Rajdhani'] font-medium">
                  New Password
                </Label>
                <Input
                  id="newPassword"
                  name="newPassword"
                  type="password"
                  value={formData.newPassword}
                  onChange={handleInputChange}
                  placeholder="Enter new password"
                  className="bg-gray-800/50 border-gray-600/50 text-gray-200 placeholder-gray-500 focus:border-cyan-500/50 focus:ring-cyan-500/20 font-['Rajdhani']"
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="confirmPassword" className="text-gray-300 font-['Rajdhani'] font-medium">
                  Confirm New Password
                </Label>
                <Input
                  id="confirmPassword"
                  name="confirmPassword"
                  type="password"
                  value={formData.confirmPassword}
                  onChange={handleInputChange}
                  placeholder="Confirm new password"
                  className="bg-gray-800/50 border-gray-600/50 text-gray-200 placeholder-gray-500 focus:border-cyan-500/50 focus:ring-cyan-500/20 font-['Rajdhani']"
                />
              </div>

              <Button
                onClick={handleChangePassword}
                disabled={isLoading || !formData.currentPassword || !formData.newPassword}
                className="w-full bg-gradient-to-r from-purple-500 to-purple-600 hover:from-purple-600 hover:to-purple-700 text-white font-['Rajdhani']"
              >
                {isLoading ? 'Changing Password...' : 'Change Password'}
              </Button>
            </CardContent>
          </Card>
        </div>

        {/* Danger Zone */}
        <Card className="bg-red-900/20 border-red-500/30 backdrop-blur-sm mt-8">
          <CardHeader>
            <CardTitle className="text-xl text-red-400 font-['Orbitron']">
              Danger Zone
            </CardTitle>
            <CardDescription className="text-red-300/70 font-['Rajdhani']">
              Irreversible actions that affect your account
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex flex-col sm:flex-row gap-4">
              <Button
                onClick={handleLogout}
                variant="outline"
                className="border-yellow-500/50 text-yellow-400 hover:bg-yellow-500/10 font-['Rajdhani']"
              >
                Sign Out
              </Button>
              <Button
                onClick={handleDeleteAccount}
                variant="outline"
                className="border-red-500/50 text-red-400 hover:bg-red-500/10 font-['Rajdhani']"
              >
                Delete Account
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
